import { Component } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import {ApicallProvider} from '../../../providers/apicall/apicall';
import {StatusViewComponent} from '../status.render.component';
@Component({
  selector: 'ngx-view-users',
  styleUrls: ['./view-users.component.scss'],
  templateUrl: './view-users.component.html',
})
export class ViewUsersComponent {
source: LocalDataSource = new LocalDataSource();

userList: Array<any>;
  settings = {
  	 hideSubHeader: true,
    edit: {
      editButtonContent: '<i class="nb-edit"></i>',
      saveButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
    },
    delete: {
      deleteButtonContent: '<i class="nb-trash"></i>',
      confirmDelete: true,
    },
    columns: {
      status : {
        title : 'Status',
        type : 'custom',
        renderComponent : StatusViewComponent,
        valuePrepareFunction: (cell,row) => row,
        onComponentInitFunction (instance) {
          instance.save.subscribe(row =>{
            console.log(row);
          })
        }
      },
      name: {
        title: 'Account ID',
        type: 'string',
      },
      firstname: {
        title: 'Name',
        type: 'string',
      },
      email: {
        title: 'Email ID',
        type: 'string',
      },
      phone: {
        title: 'Phone No',
        type: 'string',
      },
      address: {
        title: 'Address',
        type: 'string',
      },
      
    },
  };
  constructor(private service: SmartTableService,private apicall: ApicallProvider) {
   this.apicall.getallusers()
      .subscribe(res => {this.userList = res;
      	console.log(this.userList);
      	this.source.load(this.userList);
      	
      });
   
    //const data = this.service.getData();
//this.source.load(this.deviceList);
    }
    
  onDeleteConfirm(event): void {
    if (window.confirm('Are you sure you want to delete?')) {
      event.confirm.resolve();
    } else {
      event.confirm.reject();
    }
  }
  onSearch(query: string = '') {
    this.source.setFilter([
      // fields we want to inclue in the search
      {
        field: 'name',
        search: query,
      },
      {
        field: 'firstname',
        search: query,
      },
      {
        field: 'email',
        search: query,
      },
      {
        field: 'phone',
        search: query,
      },
      {
        field: 'address',
        search: query,
      },
    ], false);
    // second parameter specifying whether to perform 'AND' or 'OR' search
    // (meaning all columns should contain search query or at least one)
    // 'AND' by default, so changing to 'OR' by setting false here
  }
}



